  .logo {
    display: inline-block;
    border-radius: 2em;
    color: var(--main-teal);
    background: repeating-linear-gradient(
        20deg,
        var(--light-background),
        var(--light-background) 20%,
        var(--main-green) 21%,
        var(--main-green) 40%,
        var(--main-yellow) 41%,
        var(--main-yellow) 60%,
        var(--main-red) 61%,
        var(--main-red) 80%,
        var(--light-background) 81%,
        var(--light-background) 100%
    );
}

.logoLetter {
    font-size: 6em;
    font-weight: bolder;
    padding: .15em;
    display: inline-block;
}

.logoTextHolder {
    display: inline-block;
}

.logoText {
    color: var(--light-background);
    font-size: large;
    font-weight:bolder;
    display: block;
    transform: rotate(15deg);
}